export function sw_log_logs() {
  return `/sw_log/GetLog_List`;
}
export function sw_sws() {
  return `/sw/GetSw_List`;
}
export function create_sw_sw() {
  return `/sw_detail/Save_Sw`;
}
export function update_sw_sw() {
  return `/sw_detail/Save_Sw`;
}
export function delete_sw_sw(body) {
  return {
    data: { idxs: [`${body.sw_idx}`] },
    url: `/sw/Delete_Sw`,
  };
}
export function csv_download() {
  return `/sw/Csv_Download`;
}
